module.exports = "/*-------------------------\n\tSimple reset\n--------------------------*/\n* {\n  margin: 0;\n  padding: 0;\n}\n/*-------------------------\n\tGeneral Styles\n--------------------------*/\nhtml {\n  background: url('bg_tile.jpg') #161718;\n}\nbody {\n  background: url('bg_center.jpg') no-repeat center center;\n  min-height: 600px;\n  padding: 200px 0 0;\n  font: 14px/1.3 \"Segoe UI\", Arial, sans-serif;\n}\na, a:visited {\n  text-decoration: none;\n  outline: none;\n  color: #54a6de;\n}\na:hover {\n  text-decoration: underline;\n}\nsection, footer {\n  display: block;\n}\n/*----------------------------\n\tStyling the forms\n-----------------------------*/\n#formContainer {\n  width: 288px;\n  height: 321px;\n  margin: 0 auto;\n  position: relative;\n  z-index: 1;\n  -webkit-perspective: 800px;\n  perspective: 800px;\n}\n#formContainer form {\n  width: 100%;\n  height: 100%;\n  position: absolute;\n  top: 0;\n  left: 0;\n  /* Enabling 3d space for the transforms */\n  -webkit-transform-style: preserve-3d;\n  transform-style: preserve-3d;\n  /* When the forms are flipped, they will be hidden */\n  -webkit-backface-visibility: hidden;\n  backface-visibility: hidden;\n  /* Enabling a smooth animated transition */\n  -webkit-transition: 0.8s;\n  transition: 0.8s;\n  /* Configure a keyframe animation for Firefox */\n  /* Configure it for Chrome and Safari */\n  -webkit-animation: pulse 2s infinite;\n  /* Propiedad estandar animacion */\n  animation: pulse 2s infinite;\n}\n/* Firefox Keyframe Animation */\n/* Webkit keyframe animation */\n@-webkit-keyframes pulse {\n  0% {\n    box-shadow: 0 0 1px #008aff;\n  }\n  50% {\n    box-shadow: 0 0 10px #008aff;\n  }\n  100% {\n    box-shadow: 0 0 1px #008aff;\n  }\n}\n@keyframes pulse {\n  0% {\n    box-shadow: 0 0 1px #008aff;\n  }\n  50% {\n    box-shadow: 0 0 10px #008aff;\n  }\n  100% {\n    box-shadow: 0 0 1px #008aff;\n  }\n}\n#login {\n  background: url('login_form_bg.jpg') no-repeat;\n  z-index: 100;\n}\n#recover {\n  background: url('recover_form_bg.jpg') no-repeat;\n  z-index: 1;\n  opacity: 0;\n  /* Rotating the recover password form by default */\n  -webkit-transform: rotateY(180deg);\n  transform: rotateY(180deg);\n}\n#formContainer.flipped #login {\n  opacity: 0;\n  /**\n   * Rotating the login form when the\n   * flipped class is added to the container\n   */\n  -webkit-transform: rotateY(-180deg);\n  transform: rotateY(-180deg);\n}\n#formContainer.flipped #recover {\n  opacity: 1;\n  /* Rotating the recover div into view */\n  -webkit-transform: rotateY(0deg);\n  transform: rotateY(0deg);\n}\n/*----------------------------\n\tInputs, Buttons & Links\n-----------------------------*/\n#login .flipLink,\n#recover .flipLink {\n  /* The blue ribbon links */\n  height: 65px;\n  overflow: hidden;\n  position: absolute;\n  right: 0;\n  text-indent: -9999px;\n  top: 0;\n  width: 65px;\n}\n#recover .flipLink {\n  right: auto;\n  left: 0;\n}\n#login:after {\n  /* The \"Click here\" tooltip */\n  width: 98px;\n  height: 16px;\n  content: \"\";\n  background: url('click_here.png') no-repeat;\n  position: absolute;\n  right: -120px;\n  top: 22px;\n}\ninput[type=text], input[type=password] {\n  /* The text fields */\n  font: 15px \"Segoe UI\", Arial, sans-serif;\n  border: none;\n  background: none;\n  height: 36px;\n  left: 26px;\n  position: absolute;\n  top: 176px;\n  width: 234px;\n  text-indent: 8px;\n  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3);\n  color: #eee;\n  outline: none;\n}\n#loginPass {\n  top: 215px;\n}\n#recoverEmail {\n  top: 215px;\n}\ninput[type=submit] {\n  /* Submit button */\n  opacity: 0.9;\n  position: absolute;\n  top: 262px;\n  left: 25px;\n  width: 239px;\n  height: 36px;\n  cursor: pointer;\n  border-radius: 6px;\n  box-shadow: 0 1px 1px #888;\n  border: none;\n  color: #fff;\n  font: 14px/36px \"Segoe UI Light\", \"Segoe UI\", Arial, sans-serif;\n  /* CSS3 Gradients */\n  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(50%, #50667f), color-stop(50%, #576d88), to(#6a819b));\n  background-image: linear-gradient(to bottom, #50667f 50%, #576d88 50%, #6a819b 100%);\n  background-image: -ms-linear-gradient(to bottom, #50667f 50%, #576d88 50%, #6a819b 100%);\n  background-image: -webkit-gradient(linear, left bottom, left top, color-stop(0.5, #50667f), color-stop(0.5, #576d88), color-stop(1, #6a819b));\n}\ninput[type=submit]:hover {\n  opacity: 1;\n}\ninput::-webkit-input-placeholder {\n  color: #eee;\n}\n/*----------------------------\n\tThe Footer\n-----------------------------*/\nfooter {\n  background-color: #111111;\n  bottom: 0;\n  box-shadow: 0 -1px 2px #111111;\n  height: 45px;\n  left: 0;\n  position: fixed;\n  width: 100%;\n  z-index: 100000;\n}\nfooter h2 {\n  color: #EEEEEE;\n  font-size: 14px;\n  font-weight: normal;\n  left: 50%;\n  margin-left: -400px;\n  padding: 13px 0 0;\n  position: absolute;\n  width: 540px;\n}\nfooter h2 i {\n  font-style: normal;\n  color: #888;\n}\nfooter a.tzine, a.tzine:visited {\n  color: #999999;\n  font-size: 12px;\n  left: 50%;\n  margin: 16px 0 0 110px;\n  position: absolute;\n  text-decoration: none;\n  top: 0;\n}\nfooter a i {\n  color: #ccc;\n  font-style: normal;\n}\nfooter a i b {\n  color: #c92020;\n  font-weight: normal;\n}\n/*# sourceMappingURL=data:application/json;base64, */"